import { useNavigate } from "react-router-dom";
import { Row, Col, Card } from "react-bootstrap";
import { FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";
import { memo } from "react";
import { checkPermission } from "../../../../../actions/collaboratorActions";


const PublishComponents = ({ title, icon, link }) => {
    const navigate = useNavigate()
    return <Col sm={4}>
        <button onClick={()=> checkPermission('publish', title.includes('Website') ? 'website' : title.includes('Shop') ? 'shopon' : 'email_camp') ? navigate(link) : {}} className="btn btn-secondary py-5 py-lg-10 w-100 mb-3 mb-sm-0">
            <span className="display-3"><FontAwesomeIcon type={'light'} icon={icon} /></span>
            <p className="fs-8 mt-3 mb-0">{title}</p>
        </button>
    </Col>
}
const PublishAssets = memo(() => {
    return <Card className={`gutter-b`}>
        <Card.Body>
            <div className="h4 mb-4">Publish Your Assets</div>
            <Row className="mb-2">
                <PublishComponents title={'Publish On Website Pages'} icon="browser" link={`/website/publish/${localStorage.getItem('website_id')}`} />
                <PublishComponents title={'Shop On Bio'} icon="link" link={`/shopon-bio/preview/${localStorage.getItem('shopOn_id')}`} />
                <PublishComponents title={'Email Campaign'} icon="bullhorn" link={`/email-campaign/publish/${localStorage.getItem('emailCamp_id') ? localStorage.getItem('emailCamp_id') : ''}`} />
            </Row>
        </Card.Body>
    </Card>
})

export default PublishAssets
